import Image from "next/image";
import Link from "next/link";
import { ButtonGreen, ButtonLight } from "../Button/button";

const ContactForm = () => {
    return (
        <section className="flex flex-col md:flex-row items-center justify-center bg-[#f5f8f1] p-10">
            {/* Left - Form Section */}
            <div className="bg-[#65991d] p-10 w-full md:w-1/2 md:h-5/12 text-white ">
                <h2 className="text-3xl font-bold mb-4">Want a Free Estimate?</h2>
                <p className="mb-6">
                    We're here to bring your outdoor vision to life! Whether you need landscape design, tree trimming,
                    sod installation, or seasonal cleanup, our expert team is ready to assist.
                </p>

                <form className="space-y-4">
                    <input
                        type="text"
                        placeholder="Full Name"
                        className="w-full p-3 border border-gray-300 rounded-md text-black focus:outline-none"
                    />
                    <input
                        type="email"
                        placeholder="Email Address"
                        className="w-full p-3 border border-gray-300 rounded-md text-black focus:outline-none"
                    />
                    <input
                        type="tel"
                        placeholder="Phone Number"
                        className="w-full p-3 border border-gray-300 rounded-md text-black focus:outline-none"
                    />
                    <textarea
                        placeholder="Enter Message"
                        rows={4}
                        className="w-full p-3 border border-gray-300 rounded-md text-black focus:outline-none"
                    ></textarea>

                    {/* Submit Button */}
                    <ButtonLight text="Contact Us"/> 
                </form>
            </div>

            {/* Right - Image Section */}
            <div className="w-full md:w-5/12">
                <Image
                    src="/assets/contact/form.png" // Replace with actual path
                    alt="Person Working"
                    width={700}
                    height={500}
                    className="w-full h-auto object-cover"
                />
            </div>
        </section>
    );
};

export default ContactForm;
